.topbar {
  width: 100%;
  height: 38px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: absolute;
  top: 0px;
  background-color: transparent;
}
.actions {
  display: flex;
  height: 100%;
  align-items: center;
  margin-right: 3px;
}
.actions > div {
  padding: 4px;
  background-color: #f2f2f2;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0px 6px;
}
.action {
  height: 15px;
  width: 15px;
  background-size: cover;
  background-position: center;
  border-radius: 50%;
  opacity: 1;
}

#title {
  font-size: 17px;
}

.icon {
  height: 24px;
  width: 24px;
  opacity: 1;
  margin: 0px 5px;
}
#maximize {
  background-image: url("../images/up-arrow.png");
}
#minimize {
  background-image: url("../images/down-arrow.png");
}
.close {
  background-image: url("../images/delete.png");
}
