.notetop {
  width: 100%;
  height: 32px;
  display: flex;
  align-items: center;
  margin-top: 40px;
  padding: 0px 20px;
}

#note-name {
  width: 35%;
  max-width: 400px;
  outline: none;
  border: none;
  background-color: #d7ebfd;
  height: 100%;
  border-radius: 10px;
  padding-left: 15px;
}

#savebtn {
  width: 70px;
  height: 30px;
  background-color: #d7ebfd;
  margin: 0px 10px;
  border-radius: 6px;
  margin-right: 20px;
  font-size: 16px;
}
#font-family,
#fontsize {
  margin: 0px 15px;
  border-radius: 5px;
  height: 27px;
}
.note-body {
  font-size: 15px;
  width: 97%;
  margin: 10px;
  flex: 1;
  resize: none;
  border-radius: 8px;
  padding: 10px 25px;
  border: 0.2px solid #d7ebfd;
  outline: none;
}
select {
  border: 2px solid #d7ebfd;
}
select:active,
select:focus {
  outline: none;
}
